// @flow
import React from 'react';
import type { AbstractComponent } from 'react';
import { withStyles } from '@material-ui/core/styles';
import MuiDialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import { Close as CloseIcon } from '@material-ui/icons';

export const Dialog: AbstractComponent<typeof MuiDialog> = withStyles({
  paper: {
    borderRadius: 0,
    transition: 'opacity .2s ease-in-out',
    opacity: ({ visible }) => (visible ? '1' : '0'),
  },
})(MuiDialog);

export const DialogTitle: AbstractComponent<typeof MuiDialogTitle> = withStyles(
  {
    root: {
      padding: 0,
      textAlign: 'right',
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
    },
    closeButton: {
      padding: '0',
      '&:hover': {
        backgroundColor: 'transparent',
      },
    },
  },
)(props => {
  const { classes, onClose, children, ...rest } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...rest}>
      <div>{children}</div>
      <IconButton
        aria-label="close"
        className={classes.closeButton}
        onClick={onClose}
      >
        <CloseIcon />
      </IconButton>
    </MuiDialogTitle>
  );
});

export const DialogContent: AbstractComponent<typeof MuiDialogContent> =
  withStyles({
    root: {
      padding: 0,
    },
  })(MuiDialogContent);

export const DialogActions: AbstractComponent<typeof MuiDialogActions> =
  withStyles({
    root: {
      padding: 0,
      justifyContent: 'flex-start',
    },
  })(MuiDialogActions);
