// @flow
import React, { useState } from 'react';
import type { Element, AbstractComponent } from 'react';
import { Dialog, DialogContent, DialogTitle } from 'components/Dialog';
import type { Offer } from 'types/Offer';
import { FORM_TYPE_IDS, OFFER_STEPS } from 'shared/Consts';
import _get from 'lodash/get';
import { hideIfInvalidProps } from 'utils/RenderComponent';
import OfferFormContent from 'components/Offers/FormModalContent';
import './Style.css';
import { makeStyles } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  spacing: {
    padding: theme.spacing(2),
  },
  dialogActions: {
    padding: '1rem 0',
    marginTop: '-60px',
  },
}));

type Props = {
  open: boolean,
  offer: Offer | typeof undefined,
  handleClickCloseDialog: Function,
  isFormLoaded: boolean,
};

const checkProps = ({ offer }) => offer && _get(offer, ['OfferId']);

const CounterOfferDialog = (props: Props): Element<typeof Dialog> => {
  const { open, offer, handleClickCloseDialog, isFormLoaded } = props;

  const [isFormVisible, setIsFormVisible] = useState(false);
  const classes = useStyles();

  const stepId = _get(offer, ['StepId']);
  const formCounter = (_get(offer, ['CounterOffer', 'Forms']) || []).find(
    form => _get(form, ['FormTypeId']) === FORM_TYPE_IDS.counterOfferSubmission,
  );
  const formId = _get(formCounter, ['FormId']);
  const counterOfferId = _get(offer, ['CounterOffer', 'CounterOfferId']);

  const onAfterRenderSurveyHandler = () => {
    setIsFormVisible(true);
  };

  return (
    <Dialog
      open={open}
      aria-labelledby="customized-dialog-title"
      onClose={handleClickCloseDialog}
      scroll="paper"
      fullWidth
      maxWidth="md"
      visible={isFormVisible && isFormLoaded ? 1 : 0}
      id="counterForm"
    >
      <DialogTitle
        id="customized-dialog-title"
        onClose={handleClickCloseDialog}
        classes={{ root: classes.spacing }}
      />
      <DialogContent className={classes.spacing}>
        <div
          className={`custom-dialog-content ${
            stepId === OFFER_STEPS.advisorNeedToReviewOffer
              ? 'content-without-submit'
              : ''
          }`}
        >
          <OfferFormContent
            offerId={counterOfferId}
            formId={formId}
            handleClickCloseDialog={handleClickCloseDialog}
            onAfterRenderSurveyHandler={onAfterRenderSurveyHandler}
          />
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default (hideIfInvalidProps(checkProps)(
  CounterOfferDialog,
): AbstractComponent<Props>);
